.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: lightskyblue;
  width: 350px;
  height: auto;
  border-radius: 15px;
  padding: 20px 20px 50px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.75);
 
}
.option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px 0;
}
.password-field {
  background-color:violet;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  font-size: 18px;
  letter-spacing: 1px;
  padding: 12px 10px;
  height: 60px;
  width: 100%;
  border: 2px solid ;
  box-sizing: border-box;
}
.password-field .btn {
  position: absolute;
  top: 30%;
  right: 0;
  transform: translateY(-50%);
 height:30px;
 width:55px;
  font-size: 20px;
  margin: 10px;
}
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 15px;
}
.modal-content {
  font-weight: 700;
  border-radius: 10px;
  background-color: var(--secondary-color);
  margin: 15% auto;
  padding: 20px;
  border: none;
  width: 30%;
  text-align: center;
}
.btn {
  display: block;
  width: 60%;
  border: none;
  background-color:lavender;
  color:black;
  font-size: 16px;
  
  cursor: pointer;
  border-radius: 5px;
}